@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body {
  font-family: Audiowide;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-image: url(/ReactDrumMachine/static/media/metal02.1c4762b8.jpg);
  background-position: center;
  background-size: cover;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.inner-container {
  position: relative;
  width: 660px;
  text-align: center;
  background-image: url(/ReactDrumMachine/static/media/metal06.b44b691f.jpg);
  border-radius: 10px;
}

.inner-container .pad-bank {
  width: 332px;
  height: 272px;
  display: inline-block;
  margin: 20px;
}

.inner-container .pad-bank .drum-pad {
  position: relative;
  float: left;
  width: 100px;
  height: 80px;
  margin-right: 10px;
  border-radius: 5px;
  padding-top: 35px;
  box-sizing: border-box;
  cursor: pointer;
  color: white;
  text-shadow: 5px 5px 5px;
}

.inner-container .controls-container {
  width: 270px;
  height: 152px;
  display: inline-block;
  margin: 0px 20px 0 10px;
  vertical-align: top;
}

.inner-container .controls-container .control {
  width: 100px;
  margin: 15px auto;
  display: inline-block;
}

.inner-container .controls-container .control p {
  margin-bottom: 0;
}

.inner-container .controls-container .control .select {
  margin: auto;
}

.inner-container .controls-container #display {
  width: 150px;
  background: linear-gradient(90deg, rgba(73, 0, 95, 0.603) 0%, black 100%);
  margin: 15px auto 15px 10px;
  padding: 15px;
  box-sizing: border-box;
  border-radius: 20px;
  color: white;
  text-shadow: 5px 5px 10px white;
  box-shadow: 5px 5px 5px black;
}

.control {
  width: 100px;
  margin: 15px auto;
  display: inline-block;
  float: right;
}

.control p {
  margin-bottom: 0;
  margin-top: 0;
  color: white;
  text-shadow: 7px 5px 3px #0c0e02;
}

.control .select {
  margin: auto;
}

.outer-select, .inner-container .controls-container .control .select, .control .select {
  width: 40px;
  height: 20px;
  background: linear-gradient(90deg, rgba(73, 0, 95, 0.603) 0%, black 100%);
  background-position: center;
  background-size: auto auto;
  background-size: initial;
  border: 1px solid #0a0202;
  border-radius: 50px;
  box-shadow: 3px 3px 5px black;
}

.inner-select, .inner-container .controls-container .control .select .inner, .control .select .inner {
  width: 20px;
  height: 20px;
  background-image: url(/ReactDrumMachine/static/media/metal05.2195b27a.jpg);
  border-radius: 50px;
  border: 1px solid #3a3939;
}

.volumetext {
  margin-top: 0px;
  color: white;
  text-shadow: 5px 5px 10px white;
}

.volume-slider {
  margin-bottom: 0px;
  margin-right: 100px;
}

::-webkit-slider-runnable-track {
  height: 11px;
  background-color: #525252;
}

.logo {
  height: 50px;
  padding: 0px;
  box-sizing: border-box;
  display: inline-flex;
}

.title {
  color: white;
  font-size: 25px;
  text-shadow: 5px 5px 10px white;
}
/*# sourceMappingURL=index.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

